import React from 'react'
import HeaderHome from './HeaderHome'

const Page404 = (props) => {
    return(
        <div>
            <HeaderHome />
            <div className="container">
                <div className="row">
                    <div className="col-lg-12">
                    <h1 style={{color:'#006F83'}}>404 - No encontrado</h1>
                    <p>A pgina que voc est tentando acessar no foi encontrada ou existe.</p>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Page404


