import React from 'react'
import HeaderHome from './HeaderHome'

const Regras = (props) => {
    return(
        <div>
            <HeaderHome />
            <div className="container">
                <div className="row">
                    <div className="col-lg-12">
                    <h1 style={{color:'#006F83'}}>Regras</h1>
                    <p>Seja bem vindo ao ReactJOBS, criei este servio para facilitar a vida tanto de quem precisa de um desenvolvedor React quanto quem precisa de um emprego na rea! Espero poder ajudar voc nessa jornada, mas primeiro temos que deixar as regras claras para no atrapalharmos o trabalho de outras pessoas ok? A aplicao funciona de uma forma bem simples, porm com o tempo vou melhorar a experincia do usurio. As vagas que so postadas ficaro em destaque na home por ordem e nas categorias por ordem tambm, todas elas sero listadas em categorias e podero ser acessadas individualmente.</p>
                    <p>Os dados guardados em nosso banco de dados so livres para quem quiser ver, por isso pedimos e vamos manter somente os dados da vaga em si.</p>
                    <p><b>COMO FUNCIONA?</b><br />A empresa insere os dados da vaga em questo, os devs vo acessar a plataforma e procurar pelas vagas, caso lhe agrade dever entrar em contato com a empresa pelo e-mail cadastrado na vaga.</p>
                    <p><b>POR QUANTO TEMPO UMA VAGA FICA NO AR?</b><br />Por 30 dias. Em geral os usurios procuram mais pela vaga nos 10 primeiros dias de anncio. Motivo: os candidatos esto sempre de olho nas oportunidades novas e os recrutadores logo encontram a pessoa ideal para a vaga.</p>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Regras


